<template>
  <div class="home">
    <div class="container mt-5">
      <div class="h1 border-bottom mb-5">Kaltura Chapters Demo</div>

      <div class="row">
        <div class="col-4">
          <Chapters :chapters="chapters" @setSeconds="setSeconds($event)" />
        </div>
        <div class="col-8">
          <Viewer :seconds="seconds" />
          <textarea
            class="form-control mt-5"
            rows="10"
            v-model="description"
          ></textarea>
          <small>Each chapter needs the format:<br />00:00 Chapter Title</small>
        </div>
      </div>
      <TheFooter />
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import Chapters from "@/components/Chapters";

import Viewer from "@/components/Viewer";
import TheFooter from "@/components/TheFooter";

export default {
  name: "Home",
  components: { Chapters, Viewer, TheFooter },
  data() {
    return {
      description: "",
      seconds: 0,
    };
  },
  computed: {
    validLines() {
      let lines = this.description.split("\n");

      lines = lines.filter((line) => {
        let timestamp = line.split(" ");
        if (timestamp.length < 2) {
          return false;
        }

        if (!/[0-9]{1,2}:[0-9]{1,2}/.test(line)) {
          return false;
        }

        return true;
      });

      return lines;
    },
    chapters() {
      let chapters = this.validLines.map((line) => {
        const parts = line.split(" ");
        let timestamp = parts[0];
        let label = parts[1];
        label = label.trim();

        const timeParts = timestamp.split(":");

        let minutes = timeParts[0];
        let seconds = timeParts[1];

        let totalSeconds = parseInt(minutes) * 60 + parseInt(seconds);

        return {
          seconds: totalSeconds,
          label: label,
        };
      });

      return chapters;
    },
  },
  methods: {
    setSeconds(seconds) {
      this.seconds = seconds;
    },
  },
};
</script>
